import { closeCourse, deleteCourse, showSettings, deleteTask, publishTask, unpublishTask } from './help';
import sortable from 'common/sortable';
import 'store';
const COURSE_FUNCTION_REMASK = 'COURSE-FUNCTION-REMASK'; //课程改版功能提醒

if ($('#sortable-list').length) {
  sortable({
    element: '#sortable-list',
    ajax: false,
  }, (data) => {
    console.log(data);
    $.post($('#sortable-list').data('sortUrl'), { ids: data }, (response) => {
      var lessonNum = chapterNum = unitNum = 0;

      $('#sortable-list').find('.item-lesson, .item-chapter').each(function () {
        var $item = $(this);
        if ($item.hasClass('item-lesson')) {
          lessonNum++;
          $item.find('.number').text(lessonNum);
        } else if ($item.hasClass('item-chapter-unit')) {
          unitNum++;
          $item.find('.number').text(unitNum);
        } else if ($item.hasClass('item-chapter')) {
          chapterNum++;
          unitNum = 0;
          $item.find('.number').text(chapterNum);
        }

      });
    });
  });
}


if (!store.get(COURSE_FUNCTION_REMASK)) {
  store.set(COURSE_FUNCTION_REMASK, true);
  $('#course-function-modal').modal('show');
}

closeCourse();
deleteCourse(store);
deleteTask();
publishTask();
unpublishTask();
showSettings();
